import React from 'react';
import { graphql } from 'gatsby';
import styled from 'styled-components';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import { Link } from '@reach/router';

import Drama from '../models/Drama';
import { Layout, Wrapper } from '../components';
import dramaDefault from '../utils/drama';
import Helmet from 'react-helmet';

const HTMLTitle = '首页';

interface Props {
  data: {
    allThemeYaml: any;
  };
}

interface States {
  drama: Drama;
  title: string;
  themeCount: number;
}

const ItemTitle = styled.h2`
  text-align: center;
  font-size: 26px;
  color: #442c2e;
  line-height: 40px;
`;

const Description = styled.span`
  padding: 10px;
  display: block;
  font-size: 15px;
  color: #968082;
  line-height: 29px;
  span {
    color: #442c2e;
  }
`;

const Content = styled.div`
  background-color: #feeae6;
  padding-top: 160px;
  padding-bottom: 120px;
  padding-left: 50px;
  padding-right: 50px;
  position: relative;
  color: #968082;
`;

export default class IndexPage extends React.Component<Props, States> {
  constructor(props: any) {
    super(props);
    let drama: Drama;
    if (typeof window !== `undefined`) {
      const dramaJson: any = window.localStorage.getItem('drama');
      drama = JSON.parse(dramaJson) || dramaDefault;
    } else {
      drama = dramaDefault;
    }

    const themeCount: number = this.props.data.allThemeYaml.totalCount;

    this.state = { drama, themeCount, title: HTMLTitle };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleOnSave = this.handleOnSave.bind(this);
  }

  handleInputChange(event: any) {
    const target = event.target;
    const name: string = target.name;
    const value: any = target.value;
    const drama: Drama = Object.assign({}, this.state.drama, { [name]: value });

    this.setState({ drama });
  }

  handleOnSave() {
    if (typeof window !== `undefined`) {
      window.localStorage.setItem('drama', JSON.stringify(this.state.drama));
    } else {
      alert('保存失败');
    }
  }

  public render() {
    const { drama, title, themeCount } = this.state;

    return (
      <Layout>
        <Helmet title={title} />
        <Wrapper>
          <Content>
            <Grid container>
              <Grid item xs={4} className={`intro`}>
                <ItemTitle>
                  步骤简单
                  <Description>仅需填入剧组信息</Description>
                </ItemTitle>
              </Grid>
              <Grid item xs={4} className={`intro`}>
                <ItemTitle>
                  多种主题
                  <Description>
                    现有 <span>{themeCount}</span> 种主题
                  </Description>
                </ItemTitle>
              </Grid>
              <Grid item xs={4} className={`intro`}>
                <ItemTitle>
                  持续更新
                  <Description>更多功能与主题</Description>
                </ItemTitle>
              </Grid>
            </Grid>
            <Link to="/generate" className={`now`}>
              <Button color="primary" variant="contained" className={`button`}>
                {drama.title === dramaDefault.title ? `现在生成你的代码` : `继续编辑你的代码`}
              </Button>
            </Link>
          </Content>
        </Wrapper>
      </Layout>
    );
  }
}
export const GetThemeDefault = graphql`
  query {
    allThemeYaml(filter: { publish: { eq: true } }) {
      totalCount
    }
  }
`;
